@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;700&family=Roboto+Mono:wght@300;600;700&display=swap);
* {
  text-align: center;
  font-family: "Inter", sans-serif;
  font-family: "Roboto Mono", monospace;
}

.options-menu {
  display: flex;
  align-items: center;
  justify-content: center;
}
p:hover {
  cursor: pointer;
}
p {
  margin-left: 20px;
  margin-right: 20px;
}

.searchBar {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.searchBar-input {
  margin-right: 20px;
  height: 20px;
  width: 40vw;
  border-radius: 20px;
}

.searchBar-clear {
  cursor: pointer;
}

.title {
  display: flex;
  align-items: center;
}

.title-para {
  margin-left: 20px;
  color: white;
}

.navbar {
  display: flex;
  justify-content: space-between;
  position: -webkit-sticky;
  position: sticky;
  background-color: #ff8787;
  position: sticky;
  top: 0;
  left: 0;
}

.flex-display {
  display: flex;
}
.solid-border-styles {
  border-radius: 10px;
  border-style: solid;
  border-width: 10px;
}

/* Styles for Gallery Images */
.gallery {
  background-color: #e5ebb2;
  padding: 0;
}
.image-div-gallery {
  flex-wrap: wrap;
}
.image-div-images {
  width: 20vw;
  height: 30vh;
  margin: 20px;
  border-color: #f8c4b4;
}

/* Styles for detailed Image Section */
.detailed-image-section {
  flex-direction: column;
  align-items: center;
  justify-content: center;

  width: 100vw;
  height: 100vh;
  margin: 0px;
  position: -webkit-sticky;
  position: sticky;
  top: 70px;
  background-color: #425f57;
  background-position: 0 0, 10px 10px;
}
.detailed-image {
  width: 40vw;
  height: 40vh;
  margin: 20px;
  border-color: #bce29e;
}
.detailed-image-assets {
  justify-content: space-evenly;
  color: black;
}

/* For responsiveness */
@media screen and (max-width: 1000px) {
  .detailed-image-section {
    position: -webkit-sticky;
    position: sticky;
    top: 105px;
    width: 50vw;
    height: 100vh;
    margin: 0px;
  }
  .detailed-image {
    width: 200px;
    height: auto;
  }
  .detailed-image-assets {
    flex-direction: column;
    justify-content: space-evenly;
    color: black;
  }

  .image-div-gallery {
    align-items: center;
  }
  .image-div-images {
    width: 80%;
    height: auto;
  }
}

body {
  height: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
}

